// 購物車頁面商品卡片
<template>
    <div class="row cart-card">
        <div class="cart-card-img">
            <img class="cart-img" :src="cart.product.imageUrl" alt="">
        </div>
        <div class="cart-card-info">
            <div class="cart-card-title">{{cart.product.title}}</div>
            <div class="cart-price">US ${{cart.product.price}} / {{cart.product.unit}}</div>
            <input type="number" class="form-control cart-input" min="1"  v-model.number="tempCart.qty" @change="updateCart(cart)">
            <div class="fw-bold">${{Math.round(tempCart.total*100)/100}}</div>
            <button class="btn btn-outline-dark border-0 cart-remove-pad" type="button" @click="removeProduct(cart)"><i class="bi bi-trash h3"></i></button>
        </div>
        <div class="text-end cart-remove">
            <button class="btn btn-outline-dark border-0" type="button" @click="removeProduct(cart)"><i class="bi bi-trash h3"></i></button>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      tempCart: {} // 使用watch將props傳入的cart存入tempCard
    }
  },
  props: ['cart'],
  methods: {
    // 購物車品項數量更新驗證api
    updateCart (cart) {
      this.tempCart = cart
      this.tempCart.total = this.tempCart.qty * this.tempCart.product.price
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${cart.id}`
      const tempCart = {
        product_id: cart.product_id,
        qty: cart.qty
      }
      // this.isLoading = true
      this.$http.put(url, { data: tempCart })
        .then((res) => {
          this.$pushMessageState(res, '更新購物車')
          // this.isLoading = false
          this.$emit('update-product')
        })
    },
    // 刪除購物車品項
    removeProduct (cart) {
      // this.isLoading = true
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${cart.id}`
      this.$http.delete(url)
        .then((res) => {
          this.$pushMessageState(res, '刪除購物車')
          if (res.data.success) {
            // this.isLoading = false
            this.emitter.emit('cart-length-change')
            this.$emit('update-product')
          }
        })
    }
  },
  created () {
    this.tempCart = this.cart
  },
  watch: {
    cart () {
      this.tempCart = this.cart
    }
  },
  inject: ['emitter']
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.cart-card{
  padding: 24px 0;
  @include pad(){
    padding: 0;
  }
  .cart-card-img{
    width: 30%;
    height: 30%;
    .cart-img{
        width: 140px;
        height: 100px;
        @include pad(){
          width: 100%;
          height: 100%;
        }
    }
  }
  .cart-card-info{
    width: 50%;
    .cart-remove-pad{
      display: none;
      @include pad(){
        display: block;
      }
    }
    @include pad(){
      margin-left: 10px;
      width: 65%;
    }
  }
  .cart-remove{
    width: 20%;
    @include pad(){
      display: none;
    }
  }
  @include pad(){
    justify-content: space-between;
  }
}
    .cart-card-title{
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .cart-price{
      font-size: 15px;
      margin-bottom: 10px;
    }
    .cart-input{
      margin-bottom: 10px;
      width: 30%;
      @include pad(){
        width: 100%;
      }
    }
</style>
