// 使用者購物車頁面
<template>
  <div>
    <!-- <Loading :active="isLoading"></Loading> -->
    <StepProgress class="step-progress"></StepProgress>
    <div class="row justify-content-between">
      <div class="col-lg-7 col-10 mx-auto">
        <div class="d-flex justify-content-between align-items-center">
        <div class="cart-title fw-bold">Shopping Cart ({{carts.length}})</div>
        <router-link to="/user/index" class="cart-continue-buy text-dark fw-bold">Continue Shopping</router-link>
        </div>
          <hr>
        <div v-if="carts.length===0" class="text-center h1 mt-5">
          <router-link to="/user/index" class="text-decoration-none">
            Your Cart is empty !!<br>Let's go to look for more nuts !!
            <img src="https://media.istockphoto.com/vectors/set-of-funny-nuts-cartoon-characters-smiling-with-hands-and-legs-on-vector-id1175758858?k=6&m=1175758858&s=612x612&w=0&h=W7JqZySMHdw4Yn7TcgpRT9H85WnmMZXggd_AaefEAYs=" alt="" class="mt-1 w-100 h-100">
          </router-link>
        </div>
        <template v-for="cart in carts" :key="cart.id">
          <CartCard :cart="cart" @update-product="getCart"></CartCard>
          <hr>
        </template>
      </div>
      <div class="col-lg-4">
          <div class="d-flex flex-column">
              <div class="coupon-input-group input-group mb-3">
                <input type="text" class="form-control" v-model="coupon_code" placeholder="Please enter coupon">
                <button class="btn btn-primary" type="button" @click="addCouponCode" :disabled="is_Coupon_code">Use coupon</button>
              </div>
              <div v-if="is_Coupon_code" class="text-success fw-bold mb-3">Use coupon successfully !!</div>
          </div>
        <div class="order-table p-4 mb-5">
          <div class="h3 text-start ms-2">Review Order</div>
          <table class="table bg-order-table text-start">
            <thead>
              <tr class="order-table-tr-border fw-bold h6">
                <td class="w-50">
                  Total
                </td>
                <td class="text-end">
                  US$ {{total}}
                </td>
              </tr>
              <tr  v-if="is_Coupon_code" class="text-success order-table-tr-border fw-bold h6">
                <td>Discount</td>
                <td class="text-end">
                  20%
                </td>
              </tr>
            </thead>
            <tbody>
              <tr  v-if="is_Coupon_code" class="order-table-tr-border fw-bold h6">
                <td><del>Final_total</del></td>
                <td class="text-end">
                  <del>US$ {{total}}</del>
                </td>
              </tr>
              <tr  v-if="is_Coupon_code" class="text-success order-table-tr-border fw-bold h6">
                <td>Final_total</td>
                <td class="text-end">
                  <div class="text-success">US$ {{Math.round(final_total*100)/100}}</div>
                </td>
              </tr>
              <tr  v-else class="order-table-tr-border fw-bold h6">
                <td>Final_total</td>
                <td class="text-end">
                  <div>US$ {{Math.round(total*100)/100}}</div>
                </td>
              </tr>
            </tbody>
          </table>
          <button class="btn bg-cart-green w-100 py-3 cart-proceed-button text-white" type="button" :disabled="carts.length===0" @click="pushToOrder">Proceed to chech out</button>
        </div>
      </div>
      <div class="col-5">
            <!-- 第一版 -->
          <!-- <table class="table mt-4">
            <thead>
                <tr>
                    <th width="120"></th>
                    <th width="120">產品名稱</th>
                    <th width="120">數量</th>
                    <th width="200">價格</th>
                </tr>
                <tr v-for="cart in carts" :key="cart.id">
                    <td><button class="btn btn-outline-danger" type="button" @click="removeProduct(cart)"><i class="bi bi-x-lg"></i></button></td>
                    <td class="align-middle d-flex flex-column" v-if="is_Coupon_code">
                    {{cart.product.title}}
                    <span class="text-success" >已套用優惠卷</span>
                    </td>
                    <td class="align-middle" v-else>
                    {{cart.product.title}}
                    </td>
                    <td class="align-middle">
                    <div class="input-group">
                        <input type="number" class="form-control" min="1"  v-model.number="cart.qty" @change="updateCart(cart)">
                        <span class="input-group-text" id="basic-addon2">/{{cart.product.unit}}</span>
                    </div>
                    </td>
                    <td class="align-middle" v-if="is_Coupon_code">
                    <span class="text-success" >優惠價:</span>
                    {{$filters.currency(Math.round(cart.final_total))}}
                    </td>
                    <td class="align-middle" v-else>
                    {{$filters.currency(cart.total)}}
                    </td>
                </tr>
                <tr>
                    <td colspan="3" class="text-end">總計:</td>
                    <td>{{$filters.currency(total)}}</td>
                </tr>
                <tr v-if="is_Coupon_code">
                    <td colspan="3" class="text-end text-success">優惠總價:</td>
                    <td>{{$filters.currency(final_total)}}</td>
                </tr>
            </thead>
        </table> -->
            <!-- <div class="text-end d-flex justify-content-between mt-5">
              <router-link to="/user/products" class="btn btn-primary">繼續購物</router-link>
            </div>
            <button class="btn btn-success" type="button"><router-link to="/user/order" class="text-decoration-none text-white">前往結帳</router-link></button> -->
        </div>
    </div>
  </div>
</template>

<script>
import CartCard from '../components/CartCard.vue'
import StepProgress from '../components/StepProgress.vue'
export default {
  data () {
    return {
      // isLoading: false,
      carts: {}, // 接收api回傳的carts
      total: 0, // 總計
      coupon_code: '', // v-modal綁定input輸入
      is_Coupon_code: false, // 是否套用優惠卷
      final_total: 0 // 存取api回傳的final_total
    }
  },
  components: {
    CartCard,
    StepProgress
  },
  methods: {
    // 加入購物車
    addToCart (id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      // 加入讀取效果(暫不使用)
      // this.status.loadingItem = id
      const cart = {
        product_id: id,
        // 數量預設為一
        qty: 1
      }
      // this.isLoading = true
      this.$http.post(url, { data: cart })
        .then((res) => {
          this.$pushMessageState(res, '加入購物車成功')
          // this.isLoading = false
          this.getCart()
        })
    },
    // 取得購物車
    getCart () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      // this.isLoading = true
      this.$http.get(url)
        .then((res) => {
          this.carts = res.data.data.carts
          // this.countTotal()
          this.final_total = Math.round(res.data.data.final_total * 100) / 100
          this.total = Math.round(res.data.data.total * 100) / 100
          // this.isLoading = false
        })
    },
    // 購物車品項數量更新驗證api
    updateCart (cart) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${cart.id}`
      const tempCart = {
        product_id: cart.product_id,
        qty: cart.qty
      }
      // this.isLoading = true
      this.$http.put(url, { data: tempCart })
        .then((res) => {
          this.$pushMessageState(res, '更新購物車')
          this.getCart()
          // this.isLoading = false
        })
    },
    // 套用優惠卷api驗證
    addCouponCode () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/coupon`
      const coupon = {
        code: this.coupon_code
      }
      this.$http.post(url, { data: coupon })
        .then((res) => {
          this.$pushMessageState(res, '優惠卷套用')
          if (res.data.success) {
            this.getCart()
            // this.isLoading = false
            this.is_Coupon_code = true
          }
        })
    },
    // 刪除購物車品項
    removeProduct (cart) {
      // this.isLoading = true
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${cart.id}`
      this.$http.delete(url)
        .then((res) => {
          this.$pushMessageState(res, '刪除購物車')
          if (res.data.success) {
            // this.isLoading = false
            this.emitter.emit('cart-length-change')
            this.getCart()
          }
        })
    },
    // 幫步驟條上色
    StepProgress () {
      const text = document.querySelectorAll('.progress-text')
      text[0].style.color = '#3158d8'
    },
    // 跳轉至填寫資料頁面
    pushToOrder () {
      this.$router.push('/user/order')
    }
  },
  created () {
    // 初始化購物車列表
    this.getCart()
  },
  mounted () {
    this.StepProgress()
  },
  inject: ['emitter']
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.coupon-input-group{
  @include pad(){
    width: 90%;
    margin: 0 auto;
  }
}
.cart-continue-buy{
  font-size: 24px;
  @include pad(){
    font-size: 20px;
  }
  @include smallpad(){
    font-size: 16px;
  }
  @include iphone(){
    font-size: 14px;
  }
}
.cart-title{
  font-size: 32px;
  @include pad(){
    font-size: 20px;
  }
  @include smallpad(){
    font-size: 16px;
  }
    @include iphone(){
    font-size: 14px;
  }
}
.step-progress{
  @include smallpad(){
    display: none
  }
}
.order-table{
  background-color: #F7F5F3;
  @include pad(){
    width: 50%;
    margin: 0 auto;
  }
  @include smallpad(){
    width: 90%;
  }
  .order-table-tr-border{
    border: #F7F5F3;
  }
}
.cart-proceed-button{
  border-radius: 0.5rem !important;
  font-size: 20px;
  @include smallpad(){
    font-size: 16px;
    padding: 10px 8px !important;
  }
}
.btn:disabled{
  pointer-events:inherit;
  cursor: not-allowed;
}

</style>
